export const  BackArray = (fn) => {
  // 
  let str = fn.toString()
  let data = []
  // 
  let name = fn.name
  //  fn let OO
  let All = `\\b${name}\\b[\\d\\D]+?var(\\s+)?OO`
  let regA = new RegExp(All, 'g')
  //  var     rect  let  var
  let reg = /\bvar\b(?:\s+)?([^$]+?)(\s+)?=/g

  str.replace(regA, (match) => {
    match.replace(reg, (match, name) => {
      data.push(name)
    })
  })

  return  data
}



